/* ********************* APP LEVEL STYLES ********************* */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 15px;
  font-family: "Sarabun", sans-serif;
  font-weight: 100;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

.App {
  position: relative;
  min-height: 100vh;
}

main {
  position: relative;
  top: 6rem;
  padding-bottom: 5rem;
}

p {
  font-size: 1rem;
  padding: 1rem 2rem;
}
/* ********************* NAV AND FOOTER STYLES ********************* */
.active {
  color: #8e0537;
}

.nav {
  width: 100%;
  margin: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  background-color: rgb(243, 228, 227);
  padding: 2rem;
}

.links {
  margin-right: 10px;
  text-transform: uppercase;
}

a {
  text-decoration: none;
  color: black;
  font-size: 2rem;
  font-weight: 100;
}

a:hover {
  color: #8e0537;
}

.icon {
  height: 2.5rem;
  width: 2.5rem;
  margin: 0 0.5rem;
}

.icon:hover {
  height: 2rem;
  width: 2rem;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3.5rem;
}
/* ********************* SPLASH STYLES ********************* */
#splashname {
  font-size: 5rem;
  text-align: center;
  margin-top: 10px;
  font-family: "Fredoka One", cursive;
}

#jobtitle {
  font-size: 3rem;
  text-align: center;
}
.splashContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 30rem;
}
/* ********************* PROJECT STYLES ********************* */

.projectLinks {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.projectTitle,
.aboutTitle,
.contactTitle {
  font-family: "Fredoka One", cursive;
  font-size: 4rem;
}

.projectsContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding-bottom: 3.5rem;
  margin-top: 3rem;
}

.projectContainer {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 3.5rem;
}

.imageAndHover {
  position: relative;
}

.projectImage {
  border-radius: 1rem;
  width: 32.5rem;
  height: 26rem;
}

.hoverDiv {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: justify;
  background-color: rgba(160, 158, 158, 0.8);
  width: 32.5rem;
  border-radius: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  padding-left: 2rem;
  padding-right: 2rem;
  height: 26rem;
}

.hoverDiv p {
  font-size: 2rem;
  font-family: "Fredoka One", cursive;
  color: rgba(255, 255, 255, 1);
  padding: 0;
}
.hovLabel {
  font-family: "Fredoka One", cursive;
  font-size: 2rem;
  color: #8e0537;
}
.imageAndHover:hover .hoverDiv {
  opacity: 1;
}

/* ********************* ABOUT STYLES ********************* */

.portrait {
  width: 30rem;
  max-width: 350px;
  max-height: 250px;
  float: left;
}

.aboutTitle {
  text-align: center;
  margin-top: 2rem;
}

.about {
  width: 80%;
  margin: 0 auto;
  padding-bottom: 4rem;
}

.about p {
  font-size: 1.5rem;
  font-weight: 150;
  word-wrap: break-word;
}
/* ********************* CONTACT STYLES ********************* */
.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 10rem;
}

/* MEDIA QUERIES */

@media only screen and (max-width: 600px) {
  .projectImage {
    width: 20rem;
    height: 15rem;
  }

  .hoverDiv {
    width: 20rem;
    height: 15rem;
    background-color: white;
    opacity: 1;
    top: 17rem;
    border: 1px dotted #8e0537;
  }
  .hoverDiv p,
  .hovLabel {
    font-size: 1.2rem;
    padding: 0.1rem;
    font-weight: 500;
  }
  .imageAndHover {
    height: 32rem;
  }
  .hoverDiv p {
    color: black;
  }
  .projectLinks {
    display: none;
  }
  #splashname,
  .aboutTitle,
  .contactTitle {
    font-size: 3rem;
  }

  #jobtitle,
  .projectTitle {
    font-size: 1.5rem;
  }

  .icon {
    height: 1.5rem;
    width: 1.5rem;
  }
  a.links {
    font-size: 1.2rem;
  }
  .homeLink {
    display: none;
  }
  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .about p {
    font-size: 1.3rem;
    padding: 1rem;
  }
  .portrait {
    float: none;
    margin: 0 auto;
    width: 300px;
    height: 200px;
  }
  .contact a {
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 350px) {
  .portrait {
    width: 270px;
    height: 180px;
  }
  .contact a {
    font-size: 1.5rem;
  }
  a.links {
    font-size: 1.1rem;
  }
}
